ul.lista_transmissores {
    list-style-type: none;
    margin: 0;
    padding: 0;
    max-width: 100%;
    width: 100%;
}

li.lista_transmissores {
    padding: 1.5px 5px;
}

li.lista_transmissores:nth-child(odd){
    background-color: rgb(76, 182, 138);
}

li.lista_transmissores:hover {
    color: rgb(255, 255, 255);
    background-color: rgb(1, 110, 65);
}